body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto',
        'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
        'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}

.sidebar{margin:0;padding:0;width:500px;background:#e3ded5;display:flex;flex-direction:column;color:#43414b;overflow-y:scroll}.sidebar__container{padding:0px;margin:0px}.sidebar__container--holder{padding:16px;margin-top:6px;margin-bottom:6px;background:#fcfbfa;border-radius:4}.sidebar__total h1{display:inline}.sidebar__total h2{display:inline}.sidebar__type,.sidebar__category{height:40px;display:flex;justify-content:space-between;align-items:center}.sidebar__type h2,.sidebar__category h2{margin:10.5px,16px;display:flex;align-items:center}.sidebar__type h2 svg,.sidebar__category h2 svg{padding:0;margin:0;margin-right:10px}.sidebar__type h2 .point,.sidebar__category h2 .point{margin:0;margin-left:5px;font-size:small}.sidebar__accounts{color:#898791}.sidebar__accounts--account{height:32px;display:flex;justify-content:space-between;align-items:center;border-radius:6px;cursor:pointer}.sidebar__accounts--account:hover{background-color:#faebd7}.sidebar__accounts--account.selected{border:1px solid #000}.sidebar__accounts--account p{display:inline;margin:8px,16px;display:flex;align-items:center}.sidebar__accounts--account p .point{margin:0;margin-left:5px;font-size:small}.sidebar__accounts--account p .subtext{color:#43414b;margin-left:3px;align-self:flex-end}
.transaction-table{width:100%;border-collapse:collapse}.transaction-table--header{width:100%;height:20px}.transaction-table--header-account{width:10%}.transaction-table--header-date{width:10%}.transaction-table--header-debit{width:13%}.transaction-table--header-credit{width:13%}.transaction-table--header-description{width:20%}.transaction-table--header-tags{width:20%}.transaction-table--header-category{width:14%}.transaction-table__body__row{height:32px;margin:0;padding:0}.transaction-table__body__row--account{font-family:"Roboto",sans-serif;font-style:normal;letter-spacing:0em;text-align:center;font-weight:700;font-size:16px;line-height:18.75px;text-align:left;width:10%;border-bottom:1px solid rgba(137,135,145,.5)}.transaction-table__body__row--date{font-family:"Roboto",sans-serif;font-style:normal;letter-spacing:0em;text-align:center;font-weight:400;font-size:14px;line-height:16.41px;color:#898791;width:10%;border-bottom:1px solid rgba(137,135,145,.5)}.transaction-table__body__row--debit{width:13%}.transaction-table__body__row--credit{color:#f28018;width:13%}.transaction-table__body__row--description{width:20%;padding-left:10px}.transaction-table__body__row--tags{width:20%}.transaction-table__body__row--category{width:14%}.bubble{background-color:#7fffd4;border-radius:24px;padding:8px}.control{font-family:"Roboto",sans-serif;font-style:normal;letter-spacing:0em;text-align:center;font-weight:700;font-size:16px;line-height:18.75px;padding:16px 48px;height:48px;display:flex;justify-content:space-between}
button{padding:6px;border:1px solid #2e282a;border-radius:3px;margin:10px}
html{font-family:"Roboto",sans-serif;font-style:normal;letter-spacing:0em;text-align:center;font-weight:400;font-size:14px;line-height:16.41px;margin:0;padding:0;overflow:hidden;background-color:#e3ded5;height:100%;color:#43414b}h1{font-family:"Roboto",sans-serif;font-style:normal;letter-spacing:0em;text-align:center;font-weight:500;font-size:28px;line-height:32.81px}h2{font-family:"Roboto",sans-serif;font-style:normal;letter-spacing:0em;text-align:center;font-weight:700;font-size:16px;line-height:18.75px}p{font-family:"Roboto",sans-serif;font-style:normal;letter-spacing:0em;text-align:center;font-weight:400;font-size:14px;line-height:16.41px}body,#root{height:100%}.subtext{font-family:"Roboto",sans-serif;font-style:normal;letter-spacing:0em;text-align:center;font-weight:300;font-size:10px;font-style:italic;line-height:12px}.app{margin:0;padding:0;height:100%}.app__header{width:100%;margin:0;background:#e3ded5;display:flex;justify-content:space-between;align-items:center}.app__header__text{font-family:"Exo",sans-serif;font-size:normal;letter-spacing:0em;text-align:center;font-weight:800;font-size:40px;color:#444340;box-shadow:0px 4px 5px 0px #f6f4f140 inset;margin:.3em;padding:0;color:#43414b;margin-left:50px}.app__header svg{margin-right:10px}.app__content{display:flex;height:calc(100vh - 56px)}.app__content__main{margin:0;padding:0;width:100%;display:flex;overflow-y:scroll;flex-direction:column}.app__content__login{width:100%;display:flex;justify-content:center;align-items:center}.negative{color:#f28018}.modal{display:flex;justify-content:center;align-items:center;margin:0;padding:0;background-color:transparent}.modal__content{padding:0;display:flex;flex-direction:column;align-items:center;border-radius:4px;box-shadow:0px 2px 6px rgba(0,0,0,.25);justify-content:flex-start;background-color:#fcfbfa;margin:16px}.modal__content--header{display:flex;justify-content:space-between}.modal__content--header h1{padding-left:32px}.modal__content--input{display:flex;justify-content:space-between;align-items:center;padding:0;margin:0}.modal__content--input h2{padding-left:16px;padding-right:3px;display:inline}.modal__content--input-required{display:flex;justify-content:flex-start}.modal__content--input input,.modal__content--input select{box-sizing:content-box;margin:0;margin-right:16px;padding:1px,2px;width:150px;height:20px;border-width:1.5px;border-style:outset;border-radius:5px}.modal__content--submit{margin-top:16px}.card{padding:8px;margin:0}.card--holder{background-color:#fcfbfa;border-radius:4px;box-shadow:0px 2px 6px rgba(0,0,0,.25);padding:16px}
.daily{color:#3a5743}.special{color:#baacbd}.savings{color:#e40066}.retirement{color:#329dbf}.cash{color:#e1b92b}.investment{color:#5a8a0b}.liability{color:#574dc7}
